/* Định dạng chung cho bảng */
.card-body {
  margin: 20px 20px 70px 30px;
}

.table {
  width: 100%;
  margin: 20px 20px 0px 0px;
  border-collapse: collapse;
  max-width: 100%;
}

/* Thêm viền cho bảng */
.table,
.table th,
.table td {
  border: 1px solid #ddd;
  border-radius: 5px;
}

/* Định dạng các ô đầu tiên để căn trái */
.table th,
.table td {
  text-align: center;
}

.table th,
.table td {
  padding: 10px;
  /* Khoảng cách bên trong ô */
  text-align: left;
  /* Căn trái nội dung */
  border: 1px solid #ddd;
  /* Đường viền ô */
}

.Category {
  display: flex;
  /* Sử dụng Flexbox */
  align-items: center;
  /* Căn chỉnh các phần tử theo chiều dọc */
  justify-content: space-between;
  /* Đảm bảo các phần tử có khoảng cách giữa chúng */
}

.Category button {
  margin-left: 10px;
  /* Thêm khoảng cách giữa title và button */
}

/* Định dạng cho tiêu đề bảng (thead) */
.table thead th,
.table thead th>button {
  background-color: #4CAF50;
  /* Màu nền tiêu đề */
  color: white;
  /* Màu chữ tiêu đề */
  text-align: center;
  /* Căn giữa tiêu đề */
  padding: 12px 15px;
  font-weight: bold;
  white-space: nowrap;
  /* Không cho chữ xuống dòng */
}


.table thead th:nth-child(1) {
  width: 5% !important;
}

.table thead th:nth-child(2) {
  width: 20% !important;
}

.table thead th:nth-child(3) {
  width: 10% !important;
}

.table thead th:nth-child(4) {
  width: 5% !important;
}

.table thead th:nth-child(5) {
  width: 45% !important;
}

.table thead th:nth-child(6) {
  width: 5% !important;
}

.table thead th:last-child {
  width: 5%;
}

/* Định dạng cho cột STT */
.table tbody th[scope="row"] {
  background-color: #4CAF50;
  /* Màu nền tiêu đề */
  color: white;
  /* Màu chữ tiêu đề */
  text-align: center;
  padding: 12px 15px;
  font-weight: bold;
}

/* Màu xen kẽ cho các dòng */
.table tbody tr:nth-child(odd) {
  background-color: #d6fdd5;
}

.table tbody tr:nth-child(even) {
  background-color: #ffffff;
}

/* Hiệu ứng hover cho từng dòng */
.table tbody tr:hover {
  background-color: #d1f9a3;
  /* Màu nền xanh lá nhạt khi hover */
  transition: background-color 0.3s ease;
  /* Hiệu ứng chuyển màu mượt */
}


.table>:not(caption)>*>* {
  background-color: unset;
}

.table tbody td svg {
  color: gray;
  cursor: pointer;
  margin-left: 40px;
}