.footer {
  width: 100%;
  height: 200px;
  background-color: #121619;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 60px;
  cursor: pointer;
}

.socialMedia svg:hover {
  color: rgb(255, 175, 14);
  margin: 20px;
  font-size: 70px;
  cursor: pointer;
}

.footer p {
  margin-top: 20px;
  color: white;
}
