body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.setTablePage {
  background-image: url("../assets/background.jpg");
  /* Đường dẫn đến ảnh */
  background-size: cover;
  /* Ảnh phủ toàn bộ trang */
  background-position: center;
  /* Căn giữa ảnh */
  background-repeat: no-repeat;
  /* Không lặp lại ảnh */
  min-height: 100vh;
  /* Đảm bảo bao phủ toàn bộ chiều cao màn hình */
  display: flex;
  align-items: center;
  justify-content: center;
}

.setTableContainer {
  background-color: rgba(46, 59, 43, 0.904);
  width: 55%;
  margin: 60px auto;
  color: white;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  font-family: 'Arial', sans-serif;
  font-size: 30px;
  margin-bottom: 10px;
}

/* Sử dụng flexbox để chia form thành 2 cột */
.setTableForm {
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* 2 cột */
  gap: 80px;
  row-gap: 15px;
}

.inputGroup {
  display: flex;
  flex-direction: column;
}

.inputGroup label {
  font-size: 14px;
  margin-bottom: 5px;
}

.inputGroup input {
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #f4f4f4;
  width: 100%;
}

.submitButton {
  background-color: #ff9f00;
  color: black;
  padding: 20px 20px; /* Tăng padding ngang nếu cần thiết */
  border: none;
  font-size: 18px;
  font-weight: bold; /* Làm chữ đậm */
  text-align: center; /* Căn giữa chữ theo chiều ngang */
  display: inline-flex; /* Dùng inline-flex để nút có thể tự động mở rộng */
  align-items: center; /* Căn giữa theo chiều dọc */
  justify-content: center; /* Căn giữa theo chiều ngang */
  border-radius: 8px;
  transition: background-color 0.3s;
  grid-column: span 2;
  justify-self: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: -10px;
  width: 70%; /* Điều chỉnh chiều rộng của nút */
  height: auto; /* Đảm bảo nút không bị kéo giãn quá cao */
}


